<!-- 【通知信息】弹窗页面 -->
<template>
  <div>
    <detail-form ref="taskDialog" @reloadList="getList"/>
<!--    轮播区域-->
  <el-carousel :interval="4000"  height="300px" style="padding: 0 300px">
    <el-carousel-item v-for="item in noticeList" :key="item" style="border-radius: 30px;background: rgba(232,244,255,0.35)">
      <div style="text-align: center;">
        <div style="text-align: center;position: relative;bottom: 0;z-index: 500;font-size: 30px;font-weight: bold;">{{item.title}} </div>
        <el-image :src="formatUrl(item.picture)" style="width: 600px;height:200px;position: relative;top:0;border-radius: 10px"></el-image>
      </div>
    </el-carousel-item>
  </el-carousel>
  <div style="position: relative;left:800px;top:80px;padding: 0 50px">
    <el-input v-model="queryForm.taskTitle" placeholder="请输入查询信息" style="width: 300px;height: 50px"></el-input>
    <el-button icon="el-icon-search"  circle @click="getTaskList()"></el-button>
  </div>

  <div style="text-align: center">
    信息查询
  </div>
    <div v-if="taskList.length>0" style="padding:50px 120px">
      <div v-for="(task, index) in taskList" style="float: left;padding: 10px">
        <el-card>
          <img :src="formatUrl(task.picture)" class="image">
          <div style="padding: 14px;">
            <span>{{task.taskTitle}}</span>
            <div class="bottom clearfix">
              <time class="time">{{ task.publishTime }}</time>
              <el-button type="text" class="button" @click="handleView(task.id)">查看详情</el-button>
            </div>
          </div>
        </el-card>
      </div>
    </div>
    <div v-else style="text-align: center;padding:150px 120px">
      没有查询到相关信息...
    </div>
  </div>
</template>

<script>
  import detailForm from './detail/detailForm'
  export default {
    components: {detailForm},
    data() {
      return {
        queryForm:{taskTitle:""},
        taskList:[],
        noticeList:[],
        currentDate: new Date(),
        activeName: 'second'
      };
    },
    mounted() {
      this.getNoticeList()
      this.getTaskList()
    },
    methods: {
      //点击查看
      handleView(id) {
        this.$refs.taskDialog.openDialogView('查看信息信息', '/api/task/get?id=' + id);
      },
      getNoticeList() {
        this.$request.doPost('/api/notice/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.noticeList = res.obj.records;
          }
        })
      },
      //发布信息
      getTaskList() {
        this.$request.doPost('/api/task/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.taskList = res.obj.records;
          }
        })
      },
    }
  };
</script>
<style scoped>
  .project-name{
    float: left;
    font-size: 30px;
    text-shadow: 1px 3px 1px #ffc3a0;
    color: white;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
    margin-left: 30px;
    margin-top: 30px;
    /*margin: 0 auto; !*水平居中*!*/
  }

  .el-card{
    width: 300px;
  }

  .el-tabs--top .el-tabs__item.is-top:nth-child(2) {
    padding-left: 20px;
  }
  .el-tabs__nav-scroll{
    background: #9E0D00!important;
  }
  .el-tabs__item.is-active {
    color: white;
  }
  .el-tabs__item {
    padding: 0px 50px;
    color: white;
  }
  .el-tabs__item:hover {
    color: gainsboro;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    height: 150px;
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }
</style>





