<!-- 【任务信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysQuartz" ref="sysQuartzForm" :rules="rules" label-width="100px">
        <el-form-item label="参数" prop="params">
          <el-input v-model="sysQuartz.params" placeholder="请输入参数" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="表达式" prop="cronExpression">
          <el-input v-model="sysQuartz.cronExpression" placeholder="请输入表达式" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="jobStatus">
          <el-input v-model="sysQuartz.jobStatus" placeholder="请输入状态" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="是否执行" prop="isExec">
          <el-input v-model="sysQuartz.isExec" placeholder="请输入是否执行" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="启动时执行" prop="isBootExec">
          <el-input v-model="sysQuartz.isBootExec" placeholder="请输入启动时执行" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="目标对象" prop="beanName">
          <el-input v-model="sysQuartz.beanName" placeholder="请输入目标对象" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="名称" prop="jobName">
          <el-input v-model="sysQuartz.jobName" placeholder="请输入名称" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="最后执行时间" prop="lastExecTime">
          <el-input v-model="sysQuartz.lastExecTime" placeholder="请输入最后执行时间" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="是否有参数" prop="hasParams">
          <el-input v-model="sysQuartz.hasParams" placeholder="请输入是否有参数" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="目标方法" prop="methodName">
          <el-input v-model="sysQuartz.methodName" placeholder="请输入目标方法" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="jobType">
          <el-input v-model="sysQuartz.jobType" placeholder="请输入类型" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="备注信息" prop="remarks">
          <el-input v-model="sysQuartz.remarks" placeholder="请输入备注信息" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysQuartzForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "sysQuartzForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysQuartz: {
          params: "",
          cronExpression: "",
          jobStatus: "",
          isExec: "",
          isBootExec: "",
          beanName: "",
          jobName: "",
          lastExecTime: "",
          hasParams: "",
          methodName: "",
          jobType: "",
          remarks: ""
        },
        //表单校验
        rules: {
          params: [{required: true, message: '请输入参数', trigger: 'blur'}],
          cronExpression: [{required: true, message: '请输入表达式', trigger: 'blur'}],
          jobStatus: [{required: true, message: '请输入状态', trigger: 'blur'}],
          isExec: [{required: true, message: '请输入是否执行', trigger: 'blur'}],
          isBootExec: [{required: true, message: '请输入启动时执行', trigger: 'blur'}],
          beanName: [{required: true, message: '请输入目标对象', trigger: 'blur'}],
          jobName: [{required: true, message: '请输入名称', trigger: 'blur'}],
          lastExecTime: [{required: true, message: '请输入最后执行时间', trigger: 'blur'}],
          hasParams: [{required: true, message: '请输入是否有参数', trigger: 'blur'}],
          methodName: [{required: true, message: '请输入目标方法', trigger: 'blur'}],
          jobType: [{required: true, message: '请输入类型', trigger: 'blur'}],
          remarks: [{required: true, message: '请输入备注信息', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //打开新增页面
      openDialogAdd(title) {
        this.showDialog(title, false);
      },
      //打开查看页面
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysQuartz = res.obj;
          }
        });
      },
      //打开编辑页面
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysQuartz = res.obj;
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            that.$request.doPost('/api/sysQuartz/sub', that.sysQuartz).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.sysQuartz);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
</style>



