<!-- 【附件信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysAttach" ref="sysAttachForm" :rules="rules" label-width="100px">
      <el-form-item label="附件类型" prop="attachType">
        <el-input v-model="sysAttach.attachType" placeholder="请输入附件类型" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="文件类型" prop="fileType">
        <el-input v-model="sysAttach.fileType" placeholder="请输入文件类型" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="保存路径" prop="savePath">
        <el-input v-model="sysAttach.savePath" placeholder="请输入保存路径" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="附件名称" prop="fileName">
        <el-input v-model="sysAttach.fileName" placeholder="请输入附件名称" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="文件大小" prop="fileSize">
        <el-input v-model="sysAttach.fileSize" placeholder="请输入文件大小" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="关联id" prop="refId">
        <el-input v-model="sysAttach.refId" placeholder="请输入关联id" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="文件后缀" prop="suffix">
        <el-input v-model="sysAttach.suffix" placeholder="请输入文件后缀" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysAttachForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "sysAttachForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysAttach: {
          attachType: "",
          fileType: "",
          savePath: "",
          fileName: "",
          fileSize: "",
          refId: "",
          suffix: ""
        },
        //表单校验
        rules: {
          attachType: [{required: true, message: '请输入附件类型', trigger: 'blur'}],
          fileType: [{required: true, message: '请输入文件类型', trigger: 'blur'}],
          savePath: [{required: true, message: '请输入保存路径', trigger: 'blur'}],
          fileName: [{required: true, message: '请输入附件名称', trigger: 'blur'}],
          fileSize: [{required: true, message: '请输入文件大小', trigger: 'blur'}],
          refId: [{required: true, message: '请输入关联id', trigger: 'blur'}],
          suffix: [{required: true, message: '请输入文件后缀', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //打开新增页面
      openDialogAdd(title) {
        this.showDialog(title, false);
      },
      //打开查看页面
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysAttach = res.obj;
          }
        });
      },
      //打开编辑页面
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysAttach = res.obj;
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            that.$request.doPost('/api/sysAttach/sub', that.sysAttach).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.sysAttach);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
</style>



