<!-- 【消息信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysMessage" ref="sysMessageForm" :rules="rules" label-width="100px">
      <el-form-item label="关联用户" prop="refUserId">
        <el-input v-model="sysMessage.refUserId" placeholder="请输入关联用户" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="消息类型" prop="msgType">
        <el-input v-model="sysMessage.msgType" placeholder="请输入消息类型" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="消息内容" prop="msgContent">
        <el-input v-model="sysMessage.msgContent" placeholder="请输入消息内容" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="关联数据" prop="refId">
        <el-input v-model="sysMessage.refId" placeholder="请输入关联数据" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysMessageForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "form",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysMessage: {
        },
        //表单校验
        rules: {
        }
      }
    },
    computed: {
    },
    methods: {
      //打开新增页面
      openDialogAdd(title) {
        this.showDialog(title, false);
      },
      //打开查看页面
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysMessage = res.obj;
          }
        });
      },
      //打开编辑页面
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysMessage = res.obj;
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            that.$request.doPost('/api/sysMessage/sub', that.sysMessage).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.sysMessage);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
</style>



