<!-- 【信息信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="task" ref="taskForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="图片信息" prop="picture">
        <sui-photo :url="task.picture" @refreshPhoto="refreshPhoto" :disabled="task.taskStatus!=''&&!task.taskStatus==1"></sui-photo>
      </el-form-item>
      <el-form-item label="标题" prop="taskTitle">
        <el-input v-model="task.taskTitle" placeholder="请输入标题" :disabled="task.taskStatus!=''&&!task.taskStatus==1" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="积分" prop="taskGrade">
        <el-input v-model="task.taskGrade" placeholder="请输入积分" :disabled="task.taskStatus!=''&&!task.taskStatus==1" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="发布内容" prop="content" >
        <el-input type="textarea" v-model="task.content" placeholder="输入发布内容" :disabled="task.taskStatus!=''&&!task.taskStatus==1"/>
      </el-form-item>
<!--      <el-form-item label="附件列表" prop="fileList">-->
<!--        <sui-file @refreshFile="refreshFile" :refId="task.id" :disabled="dialog.disabled"></sui-file>-->
<!--      </el-form-item>-->
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('taskForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  import suiFile from "@/components/sui-file/sui-file";
  export default {
    components: {
      suiFile,suiPhoto,
    },
    name: "taskForm",
    data() {
      return {
        sysUserDict:[],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        task: {
          fileList:[],
          picture: "",
          taskTitle: "",
          taskGrade: "",
          publishUserId: "",
          publishTime: "",
          taskStatus: "",
          taskType: "",
          content: ""
        },
        //表单校验
        rules: {
          picture: [{required: true, message: '请选择图片信息', trigger: 'change'}],
          taskTitle: [{required: true, message: '请输入标题', trigger: 'blur'}],
          taskGrade: [{required: true, message: '请输入积分', trigger: 'blur'}],
        }
      }
    },
    computed: {
      //【发布状态】
      taskStatusDict() {
        return this.$store.getters.getDictArray('info_status');
      },
      //【发布类型】
      taskTypeDict() {
        return this.$store.getters.getDictArray('task_type');
      },
    },
    methods: {
      //图片数据
      refreshPhoto(url){
        this.task.picture = url;
      },
      //【发布者】
      getSysUserDict(){
        this.$request.doPost('/api/sysUser/getList', {}).then(res => {
          if (res.isOk) {
            this.sysUserDict=res.list;
          }
        })
      },
      //附件数据
      refreshFile(fileList){
        this.task.fileList = fileList;
      },
      //打开新增页面
      handlePublishXun(title) {
        this.showDialog(title, false);
        //加载【发布者】
        this.task.taskType=1
        this.getSysUserDict();
      },
      handlePublishZhao(title) {
        this.showDialog(title, false);
        this.task.taskType=2
        //加载【发布者】
        this.getSysUserDict();
      },
      //打开查看页面
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.task = res.obj;
            //加载【发布者】
            this.getSysUserDict();
          }
        });
      },
      //打开编辑页面
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.task = res.obj;
            //加载【发布者】
            this.getSysUserDict();
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            that.$request.doPost('/api/task/clientSub', that.task).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.task);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>
</style>



