<!-- 【用户信息】弹窗页面 -->
<template>
  <el-dialog  width="500px" center :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="登录名" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="请输入登录名" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="email">
        <el-input v-model="sysUser.password" type="password" placeholder="请输入密码" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="login('sysUserForm')">登 录</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "./../../../../../components/sui-photo/sui-photo";
  import {mapActions} from "vuex";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        roleList: [],
        checkRoleList: ["普通用户"],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysUser: {
          loginName: "",
          password: ""
        },
        //表单校验
        rules: {
        }
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['commitConfig', 'commitDictList', 'commitNoticeList', 'commitToken', 'commitLoginInfo']),
      initData() {
        const loading = this.$loading({
          lock: true,
          text: '请稍后,登录中...',
          spinner: 'el-icon-loading',
          background: 'rgba(0,0,0,0.25)'
        });
        //加载配置数据
        this.commitConfig().then(res => {
          //加载数据字典
          this.commitDictList().then(res => {
            //获取登录信息
            this.commitLoginInfo().then(res => {
              loading.close();
            })
          })
        })
      },
      login(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {//验证通过
            this.$request.doPost("/api/login/userLogin", this.sysUser).then(res => {
              if (res.isOk) {
                this.$emit('reloadList');
                this.initData();
                this.dialog.open = false;
                this.showSuccess(res.info);
              } else {
                this.showWarn(res.info);
              }
            });
          }
        });
      },
      refreshPhoto(url){
        this.sysUser.photo = url;
      },
      getRoleList(userId){
        const that = this;
        that.$request.doGet('/api/sysUser/findRolesByUserId?userId='+userId).then(res => {
          if (res.isOk) {
            that.roleList = res.list;
            this.checkRoleList = that.roleList.filter(function (v) {
              if (v.checked) {return v}
            });
          }
        });
      },
      //打开新增页面
      openDialogLogin(title) {
        this.showDialog(title, false);
      },
      //打开查看页面
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //打开编辑页面
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            if(that.checkRoleList.length==0){
              that.showWarn("请选择角色");
              return false
            }
            that.sysUser.roleList=that.checkRoleList.map(function (v) {
              return v.roleId
            });
            that.$request.doPost('/api/login/updateUserInfo',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.sysUser);
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
</style>



