<!-- 【任务信息】管理页面 -->
<template>
    <div class="app-container">
        <!-- 表单弹窗 -->
        <sysQuartz-form ref="sysQuartzDialog" @reloadList="getList"/>
        <!-- 查询条件 -->
        <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
            <el-form-item label="目标对象" prop="beanName">
                <el-input v-model="queryForm.beanName" placeholder="请输入目标对象" size="small"/>
            </el-form-item>
            <el-form-item label="名称" prop="jobName">
                <el-input v-model="queryForm.jobName" placeholder="请输入名称" size="small"/>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 查询按钮 -->
        <el-row :gutter="10" class="mb8">
            <el-col :span="1.5">
                <el-button type="primary" icon="el-icon-plus" size="mini" @click="handleAdd" v-hasPermi="['sysQuartz:add']">新增</el-button>
            </el-col>
            <el-col :span="1.5">
                <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysQuartz:delete']">批量删除</el-button>
            </el-col>
            <right-toolbar @queryTable="getList"></right-toolbar>
        </el-row>
        <!-- 表格区域 -->
        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
            <el-table-column type="selection" align="center" width="50"/>
            <el-table-column label="id"  prop="id" v-if="false"/>
            <el-table-column label="序号" type="index" width="50"/>
            <el-table-column label="表达式" prop="cronExpression" sortable />
            <el-table-column label="状态" prop="jobStatus" sortable />
            <el-table-column label="是否执行" prop="isExec" sortable />
            <el-table-column label="启动时执行" prop="isBootExec" sortable />
            <el-table-column label="目标对象" prop="beanName" sortable />
            <el-table-column label="名称" prop="jobName" sortable />
            <el-table-column label="是否有参数" prop="hasParams" sortable />
            <el-table-column label="目标方法" prop="methodName" sortable />
            <el-table-column label="操作" align="center"  width="300" fixed="right">
                <template slot-scope="scope">
                    <el-button size="mini" type="info" plain icon="el-icon-view" @click="handleView(scope.row)" v-hasPermi="['sysQuartz:view']">查看</el-button>
                    <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="handleUpdate(scope.row)" v-hasPermi="['sysQuartz:edit']">修改</el-button>
                    <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row)" v-hasPermi="['sysQuartz:delete']">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页区域 -->
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :background="true"
                :current-page="queryForm.pageNum"
                :page-sizes="[5,10,20,30,50]"
                :page-size="queryForm.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import sysQuartzForm from './quartzForm'

    export default {
        components: {sysQuartzForm},
        data() {
            return {
                loading: true,//是否加载完成
                selectedData: [],//勾选数据
                total: 0, //记录数
                dataList: [],
                queryForm: {
                    current: 1,
                    size: 10,
                    params: "",
                    cronExpression: "",
                    jobStatus: "",
                    isExec: "",
                    isBootExec: "",
                    beanName: "",
                    jobName: "",
                    lastExecTime: "",
                    hasParams: "",
                    methodName: "",
                    jobType: "",
                    remarks: ""
                }
            };
        },
        mounted() {
            //初始化数据
            this.getList();
        },
        computed: {
        },
        methods: {
            //点击搜索
            handleQuery() {
                this.queryForm.current = 1;
                this.getList();
            },
            //查询列表数据
            getList() {
                this.$request.doPost('/api/sysQuartz/getPage', this.queryForm).then(res => {
                    if (res.isOk) {
                        this.loading = false;
                        this.dataList = res.obj.records;
                        this.total = res.obj.total;
                    }
                })
            },
            //重置查询
            resetQuery() {
                this.$refs['queryForm'].resetFields();
            },
            //点击新增
            handleAdd() {
                this.$refs.sysQuartzDialog.openDialogAdd('添加任务信息');
            },
            //点击查看
            handleView(row) {
                this.$refs.sysQuartzDialog.openDialogView('查看任务信息', '/api/sysQuartz/get?id=' + row.id);
            },
            //点击修改
            handleUpdate(row) {
                this.$refs.sysQuartzDialog.openDialogEdit('修改任务信息', '/api/sysQuartz/get?id=' + row.id);
            },
            //选择页数
            handleSizeChange: function (size) {
                this.queryForm.size = size;
                this.getList();
            },
            //选择页码
            handleCurrentChange: function (currentPage) {
                this.queryForm.current = currentPage;
                this.getList();
            },
            //获取勾选数据
            handleSelectionChange(data) {
                this.selectedData = data;
            },
            //勾选批量删除
            handleDeleteAll() {
                const that = this;
                that.showConfirm('确定删除选中的除数据吗?', function () {
                    if (that.selectedData.length > 0) {
                        that.selectedData.forEach(function (item, index) {
                            that.delete(item.id);
                        });
                        that.showSuccess("删除成功")
                    } else {
                        that.showWarn("至少选择一条数据！")
                    }
                });
            },
            //选中行删除
            handleDelete(row) {
                const that = this;
                that.showConfirm('确定删除选中的除数据吗?', function () {
                    that.delete(row.id);
                    that.showSuccess("删除成功");
                });
            },
            //删除方法
            delete(id) {
                this.$request.doGet('/api/sysQuartz/delete?id=' + id).then(res => {
                    if (res.isOk) {
                        this.getList();
                    };
                });
            },
        }
    };
</script>



