<!-- 【系统日志信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysLog" ref="sysLogForm" :rules="rules" label-width="100px">
      <el-form-item label="真实名字" prop="realName">
        <el-input v-model="sysLog.realName" placeholder="请输入真实名字" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="是否手机" prop="isMobile">
        <el-input v-model="sysLog.isMobile" placeholder="请输入是否手机" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="方法名" prop="method">
        <el-input v-model="sysLog.method" placeholder="请输入方法名" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="请求端口" prop="remotePort">
        <el-input v-model="sysLog.remotePort" placeholder="请输入请求端口" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="操作日期" prop="opdate">
        <el-date-picker v-model="sysLog.opdate" placeholder="选择操作日期" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd" />
      </el-form-item>
      <el-form-item label="用户ID" prop="userId">
        <el-input v-model="sysLog.userId" placeholder="请输入用户ID" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="系统名称" prop="systemName">
        <el-input v-model="sysLog.systemName" placeholder="请输入系统名称" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="本地地址" prop="localAddr">
        <el-input v-model="sysLog.localAddr" placeholder="请输入本地地址" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="远程地址" prop="remoteAddr">
        <el-input v-model="sysLog.remoteAddr" placeholder="请输入远程地址" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="模块名称" prop="moduleName">
        <el-input v-model="sysLog.moduleName" placeholder="请输入模块名称" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="用户名称" prop="userName">
        <el-input v-model="sysLog.userName" placeholder="请输入用户名称" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="系统类型" prop="systemType">
        <el-input v-model="sysLog.systemType" placeholder="请输入系统类型" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="本地主机" prop="localName">
        <el-input v-model="sysLog.localName" placeholder="请输入本地主机" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="远程主机" prop="remoteHost">
        <el-input v-model="sysLog.remoteHost" placeholder="请输入远程主机" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="请求URI" prop="requestUri">
        <el-input v-model="sysLog.requestUri" placeholder="请输入请求URI" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysLogForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "sysLogForm",
    data() {
      return {
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysLog: {
        },
        //表单校验
        rules: {
        }
      }
    },
    computed: {
    },
    methods: {
      //打开新增页面
      openDialogAdd(title) {
        this.showDialog(title, false);
      },
      //打开查看页面
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysLog = res.obj;
          }
        });
      },
      //打开编辑页面
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysLog = res.obj;
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            that.$request.doPost('/api/sysLog/sub', that.sysLog).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.sysLog);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      },
    }
  }
</script>

<style scoped>
</style>



