<!-- 【认领信息】弹窗页面 -->
<template>
    <el-dialog center :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="claim" ref="claimForm" :rules="rules" label-width="100px" class="edit-form">
<!--            <el-form-item label="认领状态" prop="claimStatus">-->
<!--                <el-select v-model="claim.claimStatus" placeholder="选择认领状态" :disabled="dialog.disabled" >-->
<!--                    <el-option v-for="dict in claimStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>-->
<!--                </el-select>-->
<!--            </el-form-item>-->
<!--            <el-form-item label="认领时间" prop="claimDate">-->
<!--                <el-date-picker v-model="claim.claimDate" placeholder="选择认领时间" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd" />-->
<!--            </el-form-item>-->
            <el-form-item label="认领物品" prop="claimGoods">
                <el-input v-model="claim.claimGoods" placeholder="请输入认领物品" :disabled="dialog.disabled" style="width: 217px"></el-input>
            </el-form-item>
            <el-form-item label="认领人" prop="userName">
                <el-input v-model="claim.userName" placeholder="请输入认领人" :disabled="dialog.disabled" style="width: 217px"></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="userPhone">
                <el-input v-model="claim.userPhone" placeholder="请输入手机号" :disabled="dialog.disabled" style="width: 217px"></el-input>
            </el-form-item>
            <el-form-item label="认领资料说明" prop="remarks" >
                <el-input type="textarea" v-model="claim.remarks" placeholder="输入认领资料说明" :disabled="dialog.disabled"/>
            </el-form-item>
                <el-form-item label="认领资料" prop="fileList">
                    <sui-file @refreshFile="refreshFile" :refId="claim.id" :disabled="true"></sui-file>
                </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel">取 消</el-button>
            <el-button size="small" type="primary" v-if="!dialog.disabled" @click="submitForm('claimForm')">审核并归还</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import suiFile from "@/components/sui-file/sui-file";
    export default {
        components: {
            suiFile,
        },
        name: "claimForm",
        data() {
          return {
            taskId:"",
            taskDict:[],
            //弹窗设置
            dialog: {
                title: "",
                open: false,
                disabled: false
            },
            //表单数据
            claim: {
                fileList:[],
                id: "",
                claimStatus: "",
                claimDate: "",
                taskId: "",
                claimGoods: "",
                userName: "",
                userPhone: "",
                remarks: ""
            },
            //表单校验
            rules: {
                claimStatus: [{required: true, message: '请选择认领状态', trigger: 'change'}],
                claimDate: [{required: true, message: '请选择认领时间', trigger: 'change'}],
                taskId: [{required: true, message: '请选择所属信息', trigger: 'change'}],
                claimGoods: [{required: true, message: '请输入认领物品', trigger: 'blur'}],
                userName: [{required: true, message: '请输入认领人', trigger: 'blur'}],
                userPhone: [{required: true, message: '请输入手机号', trigger: 'blur'}],
            }
          }
        },
        computed: {
            //【认领状态】
            claimStatusDict() {
                return this.$store.getters.getDictArray('claim_status');
            },
        },
        methods: {
            //【所属信息】
            getTaskDict(){
                this.$request.doPost('/api/task/getList', {}).then(res => {
                    if (res.isOk) {
                        this.taskDict=res.list;
                    }
                })
            },
            //附件数据
            refreshFile(fileList){
                this.claim.fileList = fileList;
            },
            //打开查看页面
            openDialogAduit(title,url) {
              this.$request.doGet(url).then(res => {
                if (res.isOk) {
                  this.showDialog(title, false);
                  this.claim = res.obj;
                  this.taskId=res.obj.taskId
                }
              });
            },
          //我要认领
            openDialogRenling(title,id) {
                this.id=id;
                this.showDialog(title, false);
            },
            openDialogAduit2(title,url) {
              this.$request.doGet(url).then(res => {
                if (res.isOk) {
                  this.showDialog(title, true);
                  this.claim = res.obj;
                  this.taskId=res.obj.taskId
                }
              });
            },
            //取消弹窗
            cancel: function () {
                this.dialog.open = false
            },
            //提交表单
            submitForm: function (formName) {
              const that = this;
              that.claim.taskId = this.taskId;
                that.$refs[formName].validate((valid) => {
                    if (valid) {//表达校验
                        that.$request.doPost('/api/claim/sub', that.claim).then(res => {
                            if (res.isOk) {
                                that.dialog.open = false;
                                that.showSuccess("提交成功");
                                that.$emit('reloadList');
                            }else{
                              that.showWarn(res.info);
                            }
                        });
                    }
                });
            },
            //显示弹窗
            showDialog(title, disabled) {
                this.resetForm(this.claim);//重置表单
                this.dialog.open = true;//打开弹窗
                this.dialog.title = title;//设置标题
                this.dialog.disabled = disabled;//是否可编辑
            }
        }
    }
</script>

<style scoped>
</style>



