<!-- 【通知信息】管理页面 -->
<template>
  <div>
    <userLogin ref="userLoginDialog" @reloadList="reloadList"/>
    <realInfo ref="realInfoDialog" @reloadList="reloadUser"/>
    <userRegister ref="userRegisterDialog"/>
    <userInfo-form ref="userInfoDialog"/>
    <password-form ref="PasswordDialog" @reloadList="getList"/>
          <div class="welcome">
            <div style="padding: 10px" v-if="userInfo!=null&&userInfo.id">
              <el-image class="head-img" style="height: 30px;width: 30px;margin-left: 10px":src="basePath+userInfo.photo"></el-image>
              <span style="font-size: small">{{userInfo.loginName}}</span>欢迎您
            </div>
          </div>
          <div style="height: 100px;background: #800D00">
            <el-header style="text-align: right; font-size: 12px">
              <div class="project-name">{{$global.PROJECT_NAME}}</div>
              <div style="padding: 10px" v-if="userInfo!=null&&userInfo.id">
                <el-button size="mini" type="primary" plain @click="handleRealUser()">实名认证</el-button>
                <el-button size="mini" type="primary" plain @click="handleUserView()">修改信息</el-button>
                <el-button size="mini" type="primary" plain @click="handlePasswordEdit()">修改密码</el-button>
                <el-button  type="danger" @click="loginOut" size="mini" round>退出</el-button>
                <div style="padding: 10px;font-size: 20px;font-weight:bold;color: white">当前积分:{{count}}</div>
              </div>
              <div v-else style="padding: 10px">
                <el-button size="mini" type="primary" plain  @click="userLogin">登录</el-button>
                <el-button size="mini" type="primary" plain  @click="userRegister">注册</el-button>
              </div>
            </el-header>
          </div>

          <el-tabs style="background: white;" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="网站首页" name="1">
              <home ref="home"></home>
            </el-tab-pane>
            <el-tab-pane label="寻物启事" name="2">
              <xunwu ref="xunwu"></xunwu>
            </el-tab-pane>
            <el-tab-pane label="失物招领" name="3">
              <zhaoling ref="zhaoling"></zhaoling>
            </el-tab-pane>
            <el-tab-pane label="个人中心" name="4">
              <myinfo ref="myinfo"></myinfo>
            </el-tab-pane>
          </el-tabs>
  </div>
</template>
<style scoped>
  .head-img {
    position: relative;top:10px;
    width: 35px;
    height: 35px;
    border-radius: 50%
  }
  .el-tabs__nav-scroll{
    padding-left: 30px;
  }
  .welcome{
    float: left;
    font-size: 20px;
    text-shadow: 1px 3px 3px #000000;
    color: #e8f4ff;
    font-weight: bold;
    text-align: center;
  }
  .project-name{
    float: left;
    font-size: 30px;
    text-shadow: 1px 3px 3px #000000;
    color: #e8f4ff;
    font-weight: bold;
    text-align: center;
    margin: 30px 30px; /*水平居中*/
  }
</style>
<script>
  import home from './tab/home'
  import xunwu from './tab/xunwu'
  import zhaoling from './tab/zhaoling'
  import myinfo from './tab/myinfo'
  import userInfoForm from "./tab/user/userInfo";
  import realInfo from "./tab/user/realInfo";
  import passwordForm from "./tab/user/userPassword";
  import userLogin from './tab/user/userLogin'
  import userRegister from './tab/user/userRegister'
  import {mapActions} from "vuex";
  export default {
    components: {home,xunwu,zhaoling,myinfo,userInfoForm,passwordForm,userLogin,userRegister,realInfo},
    computed:{
      userInfo(){
        return this.$store.getters.userInfo
      },
      basePath(){
        return this.$global.FILE_BASE_PATH
      }
    },
    mounted() {
      this.getUserIntegral();
    },
    methods: {
      ...mapActions(['commitLoginInfo']),
      loginOut(){
        const that = this;
        that.showConfirm('确认退出吗?',function (){
          that.$request.doGet("/api/login/loginOut").then(res=>{
            if(res.isOk){
              that.commitLoginInfo();//更新登录信息
              that.showToast("退出成功");
            }
          })
        })
      },
      reloadUser(){
        this.commitLoginInfo();//更新登录信息
      },
      getUserIntegral(){
        let id = this.$store.getters.userInfo.id;
        if(id!=""){
          this.$request.doGet("/api/integral/getPage?userId="+this.$store.getters.userInfo.id).then(res=>{
            if(res.isOk){
              let count = 0
              let list = res.obj.records;
              if(list!=null){
                for(let i=0;i<list.length;i++){
                  let grade = list[i].grade;
                  count = count+parseInt(grade)
                }
              }
              this.count=count;
            }
          })
        }
      },
      handleClick(tab, event) {
        if(this.activeName==1){
          this.$refs.home.getTaskList()
        }
        if(this.activeName==2){
          this.$refs.xunwu.getTaskList()()
        }
        if(this.activeName==3){
          this.$refs.zhaoling.getTaskList()
        }
        if(this.activeName==4){
          if(!(this.userInfo!=null&&this.userInfo.id)){
            this.$refs.userLoginDialog.openDialogLogin('用户登录', '/api/sysUser/get');
          }
          this.$refs.myinfo.getList()
        }
      },
      reloadList(){
        this.activeName='1'
      },
      userLogin() {
        this.$refs.userLoginDialog.openDialogLogin('用户登录', '/api/sysUser/get');
      },
      userRegister() {
        this.$refs.userRegisterDialog.openDialogRegister('用户注册', '/api/sysUser/get');
      },
      handleUserView() {
        this.$refs.userInfoDialog.openDialogEdit('用户信息', '/api/sysUser/get?id=' + this.$store.getters.userInfo.id);
      },
      handleRealUser() {
        this.$refs.realInfoDialog.openDialogEdit('实名认证', '/api/sysUser/get?id=' + this.$store.getters.userInfo.id);
      },
      handlePasswordEdit() {
        this.$refs.PasswordDialog.openDialogEdit('修改密码', '/api/sysUser/get?id=' + this.$store.getters.userInfo.id);
      },
    },

    data() {
      return {
        count:0,
        activeName: '1'
      };
    },
  }
</script>




