<!-- 【通知信息】弹窗页面 -->
<template>
  <div>
    <detail-form ref="taskDialog" @reloadList="getList"/>
    <renlingForm ref="renlingFormDialog" />
    <div style="text-align: center">
      失物招领信息
    </div>
    <div style="padding:50px 120px">
      <div v-for="(task, index) in taskList" style="float: left;padding: 10px">
        <el-card>
          <img :src="formatUrl(task.picture)" class="image">
          <div style="padding: 14px;">
            <span>{{task.taskTitle}}</span>
            <div class="bottom clearfix">
              <time class="time">{{ task.publishTime }}</time>
              <el-button size="mini" type="danger" :disabled="task.hasUser" @click="renling(task.id)">我要认领</el-button>
              <el-button type="text" class="button" @click="handleView(task.id)">查看详情</el-button>
            </div>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
  import detailForm from './detail/detailForm'
  import renlingForm from './detail/renlingForm'
  export default {
    components: {detailForm,renlingForm},
    data() {
      return {
        taskId:"",
        taskList:[],
        noticeList:[],
        currentDate: new Date(),
        activeName: 'second'
      };
    },
    mounted() {
      this.getTaskList()
    },
    methods: {
      //点击查看
      handleView(id) {
        this.$refs.taskDialog.openDialogView('查看信息信息', '/api/task/get?id=' + id);
      },
      renling(id) {
        let hasLogin = this.$store.getters.hasLogin;
        if(!hasLogin){
          this.showWarn("请先进行登录和实名认证后才能使用")
          return
        }
        let userName = this.$store.getters.userInfo.userName;
        if(userName==""){
          this.showWarn("招物认领需要实名认证后才能使用")
          return
        }
        this.$refs.renlingFormDialog.openDialogRenling('认领资料填写',id);
        this.$refs.taskId=id;
        // this.$request.doPost('/api/claim/sub', {taskId:id}).then(res => {
        //   if (res.isOk) {
        //     this.showSuccess("认领申请成功")
        //   }else{
        //     this.showWarn(res.info)
        //   }
        // })
      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      //发布信息
      getTaskList() {
        this.$request.doPost('/api/task/getPage', {taskType:1}).then(res => {
          if (res.isOk) {
            this.taskList = res.obj.records;
          }
        })
      },
    }
  };
</script>
<style scoped>
  .project-name{
    float: left;
    font-size: 30px;
    text-shadow: 1px 3px 1px #ffc3a0;
    color: white;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
    margin-left: 30px;
    margin-top: 30px;
    /*margin: 0 auto; !*水平居中*!*/
  }

  .el-card{
    width: 300px;
  }

  .el-tabs--top .el-tabs__item.is-top:nth-child(2) {
    padding-left: 20px;
  }
  .el-tabs__nav-scroll{
    background: #9E0D00!important;
  }
  .el-tabs__item.is-active {
    color: white;
  }
  .el-tabs__item {
    padding: 0px 50px;
    color: white;
  }
  .el-tabs__item:hover {
    color: gainsboro;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    height: 150px;
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }
</style>





