<!-- 【用户信息】管理页面 -->
<template>
  <div class="app-container">
    <!-- 表单弹窗 -->
    <sysUser-form ref="dialog" @reloadList="getList"/>
    <!-- 查询条件 -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="登录名" prop="loginName">
        <el-input v-model="queryForm.loginName" placeholder="请输入登录名" size="small"/>
      </el-form-item>
      <el-form-item label="姓名" prop="userName">
        <el-input v-model="queryForm.userName" placeholder="请输入姓名" size="small"/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
      </el-form-item>
    </el-form>
    <!-- 查询按钮 -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="handleAdd" v-hasPermi="['sysUser:add']">新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysUser:delete']">批量删除</el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!-- 表格区域 -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id"  prop="id" v-if="false"/>
      <el-table-column label="序号" type="index" />
      <el-table-column label="登录名" prop="loginName" sortable />
      <el-table-column label="姓名" prop="userName" sortable />
      <el-table-column label="操作" align="center"  width="500" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="handleView(scope.row)" v-hasPermi="['sysUser:view']">查看</el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="handleUpdate(scope.row)" v-hasPermi="['sysUser:edit']">修改</el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="handleReset(scope.row)" v-hasPermi="['sysUser:edit']">重置密码</el-button>
          <el-button v-if="scope.row.disabled==1" size="mini" type="danger" plain icon="el-icon-edit" @click="handleUnLock(scope.row)" v-hasPermi="['sysUser:edit']">释放</el-button>
          <el-button v-else size="mini" type="primary" plain icon="el-icon-edit" @click="handleLock(scope.row)" v-hasPermi="['sysUser:edit']">冻结</el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row)" v-hasPermi="['sysUser:delete']">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页区域 -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.pageNum"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysUserForm from './userForm'

  export default {
    components: {sysUserForm},
    data() {
      return {
        loading: true,//是否加载完成
        selectedData: [],//勾选数据
        total: 0, //记录数
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          photo: "",
          loginName: "",
          password: "",
          userName: "",
          userNo: "",
          email: "",
          phone: "",
          officeId: ""
        }
      };
    },
    mounted() {
      //初始化数据
      this.getList();
    },
    computed: {
    },
    methods: {
      //点击搜索
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //查询列表数据
      getList() {
        this.$request.doPost('/api/sysUser/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //重置查询
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //点击新增
      handleAdd() {
        this.$refs.dialog.openDialogAdd('添加用户信息');
      },
      //点击查看
      handleView(row) {
        this.$refs.dialog.openDialogView('查看用户信息', '/api/sysUser/get?id=' + row.id);
      },
      //点击修改
      handleUpdate(row) {
        this.$refs.dialog.openDialogEdit('修改用户信息', '/api/sysUser/get?id=' + row.id);
      },
      //重置密码
      handleReset(row) {
        const that = this;
        that.showConfirm("确认重置该用户的密码吗？",function () {
          that.$request.doGet('/api/sysUser/resetPassword?userId=' + row.id).then(res => {
            if (res.isOk) {
              that.getList();
              that.showSuccess(res.info);
            };
          });
        })
      },
      //冻结用户
      handleLock(row) {
        const that = this;
        that.showConfirm("确认冻结该用户吗？",function () {
          that.$request.doGet('/api/sysUser/lockUser?id=' + row.id+"&disabled=1").then(res => {
            if (res.isOk) {
              that.getList();
              that.showSuccess(res.info);
            };
          });
        })
      },
      //释放用户
      handleUnLock(row) {
        const that = this;
        that.showConfirm("确认释放该用户吗？",function () {
          that.$request.doGet('/api/sysUser/lockUser?id=' + row.id+"&disabled=0").then(res => {
            if (res.isOk) {
              that.getList();
              that.showSuccess(res.info);
            };
          });
        })
      },
      //选择页数
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //选择页码
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //获取勾选数据
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //勾选批量删除
      handleDeleteAll() {
        const that = this;
        that.showConfirm('确定删除选中的除数据吗?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("删除成功")
          } else {
            that.showWarn("至少选择一条数据！")
          }
        });
      },
      //选中行删除
      handleDelete(row) {
        const that = this;
        that.showConfirm('确定删除选中的除数据吗?', function () {
          that.delete(row.id);
          that.showSuccess("删除成功");
        });
      },
      //删除方法
      delete(id) {
        this.$request.doGet('/api/sysUser/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
    }
  };
</script>



