<template>
  <div>
    <el-upload
      class="upload-demo"
      :disabled="disabled"
      action="/api/sysAttach/uploadFile/file/test"
      :on-preview="handlePreview"
      :before-remove="beforeRemove"
      :on-success="handleAvatarSuccess"
      :file-list="fileList"
      list-type="picture">
      <el-button v-if="!disabled" size="small" type="primary">点击上传</el-button>
      <div v-if="!disabled" slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
    </el-upload>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        fileList: []
      };
    },
    props: {
      refId: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    watch: {
      refId(refId) {
        this.getFileList(refId)
      }
    },
    mounted() {
      this.getFileList(this.refId)
    },
    methods: {
      getFileList(refId) {
        const that = this;
        this.$request.getFileHistory({refId: refId}).then(res => {
          if (res.isOk) {
            that.fileList = res.list.map(function (v) {
              return {id: v.id, name: v.fileName, url: that.formatUrl(v.savePath)}
            });
          }
        })
      },
      handleAvatarSuccess(res, file) {
        const that = this;
        that.fileList = that.fileList.concat({
          id: res.obj.id,
          name: res.obj.fileName,
          url: that.formatUrl(res.obj.savePath)
        });
        let fileIds = that.fileList.map(function (v) {
          return v.id
        });
        that.$emit('refreshFile', fileIds);
      },
      //移除文件
      beforeRemove(file, fileList) {
        const that = this;
        that.showConfirm("确认删除文件吗?", function () {
          that.deleteFile(file);
        });
        return false; // 这是重点,不管上面的操作结果如何都返回false
      },
      //删除方法
      deleteFile(file) {
        const that = this;
        that.$request.deleteFile({id: file.id}).then(res => {
          if (res.isOk) {
            that.fileList = that.fileList.filter(function (v) {
              if (v.uid !== file.uid) {
                return v
              }
            });
          }
        })
      }
    },
    handleRemove(file, fileList) {
      //该方法beforeRemove返回true时执行，需要同步处理
    },
    handlePreview(file) {
      console.log(file);
    }
  }
</script>



