<!-- 【通知信息】弹窗页面 -->
<template>
  <div>
    <claimForm ref="claimFormDialog" @reloadList="reloadList"/>
    <taskForm ref="taskDialog" @reloadList="getList"/>
    <div style="text-align: center;font-weight: bold;font-size: larger">我的失物招领</div>
    <el-button size="small" type="primary" @click="handlePublishXun">发布失物招领</el-button>
    <div v-if="hasLogin" style="padding:50px 120px">
      <div v-for="(task, index) in xunList" style="float: left;padding: 10px">
        <el-card>
          <img :src="formatUrl(task.picture)" class="image">
          <div style="padding: 14px;">
            <span>{{task.taskTitle}}</span>
            <div class="bottom clearfix">
              <time class="time">{{ task.publishTime }}</time>
<!--             {{task.hasUser}}-->
              <div v-if="task.taskStatus==3">
                <el-button size="mini" >已下架</el-button>
                <el-button size="mini" @click="renlingView2(task.id)">查看认领人</el-button>
              </div>
              <div v-if="task.taskStatus==2&&task.hasUser">
                <el-badge value="new" class="item">
                  <el-button size="mini" type="danger" @click="renlingView(task.id)">有人认领</el-button>
                </el-badge>
              </div>
              <el-badge  class="item" style="padding-left: 5px">
                <el-button type="text" class="button" style="margin-left: 5px" @click="handlePublishView(task.id)">查看详情</el-button>
              </el-badge>
            </div>
          </div>
        </el-card>
      </div>
    </div>
    <div style="clear:both"></div>
    <div style="text-align: center;font-weight: bold;font-size: larger;padding-top: 100px">我的寻物启事</div>
    <el-button size="small" type="primary" @click="handlePublishZhao">发布寻物启事</el-button>
    <div v-if="hasLogin" style="padding:50px 120px">
      <div v-for="(task, index) in zhaoList" style="float: left;padding: 10px">
        <el-card>
          <img :src="formatUrl(task.picture)" class="image">
          <div style="padding: 14px;">
            <span>{{task.taskTitle}}</span>
            <div class="bottom clearfix">
              <time class="time">{{ task.publishTime }}</time>
              <el-badge value="new" class="item">
              <el-button type="text" class="button" @click="handlePublishView(task.id)">查看详情</el-button>
              </el-badge>
            </div>
          </div>
        </el-card>
      </div>
    </div>

    <div style="clear: both"></div>
    <div style="text-align: center;font-weight: bold;font-size: larger;padding-top: 100px">我的认领记录</div>
    <div v-if="hasLogin" style="padding:50px 120px">
      <div v-for="(task, index) in renList" style="float: left;padding: 10px">
        <el-card>
          <img :src="formatUrl(task.picture)" class="image">
          <div style="padding: 14px;">
            <span>{{task.taskTitle}}</span>
            <div class="bottom clearfix">
              <time class="time">{{ task.publishTime }}</time>
                <el-button type="text" class="button" @click="handlePublishView(task.id)">查看详情</el-button>
            </div>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
  import claimForm from './detail/claimForm'
  import taskForm from './user/taskForm'
  export default {
    components: {taskForm,claimForm},
    data() {
      return {
        xunList:[],
        zhaoList:[],
        renList:[],
        noticeList:[],
        currentDate: new Date(),
        activeName: 'second'
      };
    },
    mounted() {
      this.getList()
    },
    computed: {
      hasLogin() {
        return this.$store.getters.hasLogin;
      }
    },
    methods: {
      reloadList(){
        this.getList()
      },
      getList(){
        this.getXunList()
        this.getZhaoList()
      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      //发布
      handlePublishView(id) {
        this.$refs.taskDialog.openDialogView('查看信息', '/api/task/get?id=' + id);;
      },
      //认领资料查看
      renlingView(id) {
        this.$request.doGet('/api/claim/getList?taskId='+id).then(res => {
          if (res.isOk) {
            let claimId = res.list[0].id;
            this.$refs.claimFormDialog.openDialogAduit('查看认领', '/api/claim/get?id=' + claimId);
          }
        })
      },
      renlingView2(id) {
        this.$request.doGet('/api/claim/getList?taskId='+id).then(res => {
          if (res.isOk) {
            let claimId = res.list[0].id;
            this.$refs.claimFormDialog.openDialogAduit2('查看认领', '/api/claim/get?id=' + claimId);
          }
        })
      },
      //发布
      handlePublishXun() {
        let hasLogin = this.$store.getters.hasLogin;
        if(!hasLogin){
          this.showWarn("请先进行登录和实名认证后才能使用");
          return
        }
        let userName = this.$store.getters.userInfo.userName;
        if(userName==""){
          this.showWarn("寻物任务需要实名认证后才能使用");
          return
        }
        this.$refs.taskDialog.handlePublishXun("失物招领");
      },
      //发布
      handlePublishZhao() {
        let hasLogin = this.$store.getters.hasLogin;
        if(!hasLogin){
          this.showWarn("请先进行登录和实名认证后才能使用");
          return
        }
        let userName = this.$store.getters.userInfo.userName;
        if(userName==""){
          this.showWarn("招物任务需要实名认证后才能使用");
          return
        }
        this.$refs.taskDialog.handlePublishZhao("寻物启事");
      },
      //寻物
      getXunList() {
        this.$request.doGet('/api/task/getClientPage?taskType=1').then(res => {
          if (res.isOk) {
            this.xunList = res.obj.records;
          }
        })
      },
      //招物
      getZhaoList() {
        this.$request.doGet('/api/task/getClientPage?taskType=2').then(res => {
          if (res.isOk) {
            this.zhaoList = res.obj.records;
          }
        })
      },
      //认领记录
      getZhaoList() {
        this.$request.doGet('/api/claim/getRengPage?userName='+this.$store.getters.userInfo.userName).then(res => {
          if (res.isOk) {
            this.renList = res.obj.records;
          }
        })
      },
    }
  };
</script>
<style scoped>
  .project-name{
    float: left;
    font-size: 30px;
    text-shadow: 1px 3px 1px #ffc3a0;
    color: white;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
    margin-left: 30px;
    margin-top: 30px;
    /*margin: 0 auto; !*水平居中*!*/
  }

  .el-card{
    width: 300px;
  }

  .el-tabs--top .el-tabs__item.is-top:nth-child(2) {
    padding-left: 20px;
  }
  .el-tabs__nav-scroll{
    background: #9E0D00!important;
  }
  .el-tabs__item.is-active {
    color: white;
  }
  .el-tabs__item {
    padding: 0px 50px;
    color: white;
  }
  .el-tabs__item:hover {
    color: gainsboro;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    height: 150px;
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }
</style>





